import React, { useState } from "react"
import { Section, Button, ButtonLink, Empty } from "components"
import { Formik } from "formik"
import { Form, Row, Col, List, Popconfirm, Skeleton } from "antd"
import TextInput from "components/forms/TextInput"
import { Trans, t } from "@lingui/macro"
import CheckboxInput from "components/forms/CheckboxInput"
import DateInput from "components/forms/DateInput"
import styled from "styled-components"
import { withI18n } from "@lingui/react"
import moment from "moment"
import { baseStyles } from "styles/base"
import { useMutation } from "react-apollo"
import { mutateUpdateEducations, mutateDeleteEducations } from "queries/profile"
import { educationOptions } from "store/actions/userActions"
import { SubmitButton } from "formik-antd"

const FieldRow = styled.div`
	position: relative;
	margin-bottom: 2em;
	padding-bottom: 2em;
	border-bottom: 1px solid #ddd;
	&:hover {
		.delete {
			display: flex;
			justify-content: center;
			align-items: center;
			visibility: visible;
		}
	}
`

const StyledListItem = styled(List.Item)`
	&& {
		margin: 0 0 0.5em 0;
		padding-left: 0;
		padding-top: 0;
		.ant-list-item-meta-content {
			.ant-list-item-meta-title {
				color: ${baseStyles.greyColor};
				font-size: 14px;
				margin-bottom: 0;
			}
			.ant-list-item-meta-description {
				margin-bottom: 0.5em;
			}
		}
	}
`

function EducationList({ unauthorizedUser, education, loading }) {
	return (
		<List
			itemLayout="horizontal"
			dataSource={education}
			locale={{
				emptyText: !loading ? (
					<Empty
						description={
							unauthorizedUser ? (
								<Trans>This tutor haven't set their education yet</Trans>
							) : (
								<Trans>You haven't set your education yet</Trans>
							)
						}
					/>
				) : null
			}}
			renderItem={item => {
				const fromDate = `${moment(item.fromDate).format("MMM YYYY")}`
				const toDate = `${moment(item.toDate).format("MMM YYYY")}`

				return (
					<StyledListItem>
						<Skeleton loading={loading} active>
							<List.Item.Meta title={item.institution} description={`${fromDate} - ${item.isPresent ? "present" : toDate}`} />
						</Skeleton>
					</StyledListItem>
				)
			}}
		/>
	)
}

const RenderForms = ({ handlers, isAdding, i18n, education }) => {
	const { handleAddCertificate, handleCancel, handleDelete, setIsAdding, setEditMode } = handlers

	if (isAdding) {
		return (
			<Formik initialValues={{ isPresent: false }} onSubmit={handleAddCertificate}>
				{({ handleSubmit, values }) => (
					<Form layout="vertical" onSubmit={handleSubmit}>
						<TextInput
							name="institution"
							label={<Trans>School/college</Trans>}
							placeholder={i18n._(t`Your school/college`)}
							marginBottom={0}
						/>
						<Row className="mb1em">
							<Col lg={24}>
								<CheckboxInput name="isPresent">
									<Trans>I'm currently studying here</Trans>
								</CheckboxInput>
							</Col>
						</Row>
						<Row gutter={16}>
							<Col lg={12} xs={12}>
								<DateInput
									monthly
									name="fromDate"
									placeholder={i18n._(t`Date joined`)}
									disabledDate={current => current && current > moment()}
								/>
							</Col>
							<Col lg={12} xs={12}>
								<DateInput
									monthly
									name="toDate"
									placeholder={i18n._(t`Until`)}
									disabledDate={current => current && current > moment()}
									disabled={values.isPresent}
								/>
							</Col>
						</Row>
						<SubmitButton block>
							<Trans>Save this education</Trans>
						</SubmitButton>{" "}
						&nbsp;{" "}
						<ButtonLink block icon="close" onClick={handleCancel}>
							&nbsp; <Trans>Cancel</Trans>
						</ButtonLink>
					</Form>
				)}
			</Formik>
		)
	}

	return (
		<div>
			<Row>
				<Col lg={24}>
					{education &&
						education.map((item, idx) => (
							<FieldRow key={item.pvid}>
								<Formik initialValues={item} onSubmit={handleAddCertificate}>
									{({ handleSubmit, values }) => (
										<Form layout="vertical" onSubmit={handleSubmit}>
											<TextInput
												name="institution"
												label={<Trans>School/college</Trans>}
												placeholder={i18n._(t`Your school/college`)}
												marginBottom={0}
											/>
											<Row className="mb1em">
												<Col lg={24}>
													<CheckboxInput name="isPresent">
														<Trans>I'm currently studying here</Trans>
													</CheckboxInput>
												</Col>
											</Row>

											<Row gutter={16}>
												<Col lg={12} xs={12}>
													<DateInput
														monthly
														name="fromDate"
														placeholder={i18n._(t`Date joined`)}
														disabledDate={current => current && current > moment()}
													/>
												</Col>
												<Col lg={12} xs={12}>
													<DateInput
														monthly
														name="toDate"
														placeholder={i18n._(t`Until`)}
														disabledDate={current => current && current > moment()}
														disabled={values.isPresent}
													/>
												</Col>
											</Row>
											<Row>
												<Col lg={24}>
													<SubmitButton type="primary">
														<Trans>Update</Trans>
													</SubmitButton>{" "}
													&nbsp;{" "}
													<Popconfirm
														title={<Trans>Delete this education?</Trans>}
														onConfirm={() => handleDelete(item.pvid)}
													>
														<ButtonLink icon="close">
															&nbsp; <Trans>Delete</Trans>
														</ButtonLink>
													</Popconfirm>
												</Col>
											</Row>
										</Form>
									)}
								</Formik>
							</FieldRow>
						))}
				</Col>
			</Row>
			<Row>
				<Col lg={24}>
					<Button block type="dashed" size="medium" icon="plus" className="mb2em" onClick={() => setIsAdding(true)}>
						&nbsp; <Trans>Add a new education</Trans>
					</Button>
					<ButtonLink block icon="close" onClick={() => setEditMode("")}>
						&nbsp; <Trans>Cancel</Trans>
					</ButtonLink>
				</Col>
			</Row>
		</div>
	)
}

function Education({ onEditMode, unauthorizedUser, onEducation, i18n, fetchUser, loading }) {
	const { editMode, setEditMode } = onEditMode
	const { education, setEducation } = onEducation
	const [isAdding, setIsAdding] = useState(false)

	const [updateEducations] = useMutation(
		mutateUpdateEducations,
		educationOptions(setEditMode, fetchUser, i18n._(t`Your education has been updated`))
	)
	const [deleteEducations] = useMutation(
		mutateDeleteEducations,
		educationOptions(setEditMode, fetchUser, i18n._(t`Your education has been deleted`))
	)

	const handleCancel = () => {
		setIsAdding(false)
		setEditMode("")
	}

	const handleAddCertificate = (values, { setSubmitting }) => {
		values = {
			...values,
			fromDate: moment(values.fromDate).format("YYYY-MM") + "-01",
			toDate: moment(values.toDate).format("YYYY-MM") + "-01"
		}
		updateEducations({ variables: { list: [values] } }).finally(() => setSubmitting(false))
	}

	const handleDelete = pvid => {
		deleteEducations({ variables: { pvidList: pvid } }).finally(() => handleCancel())
	}

	if (loading) return <Skeleton active />

	return (
		<Section noPadding>
			{editMode === "education" && !unauthorizedUser ? (
				<RenderForms
					isAdding={isAdding}
					education={education}
					i18n={i18n}
					handlers={{ handleDelete, handleAddCertificate, handleCancel, setIsAdding, setEditMode }}
				/>
			) : (
				<EducationList education={education} unauthorizedUser={unauthorizedUser} loading={loading} />
			)}
		</Section>
	)
}

export default withI18n()(Education)
